/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.App {
  text-align: center;
}

.nameAndSocial {
  padding-top: 12%;
  opacity: 0;
  animation: fadeIn 2s;
  animation-fill-mode: forwards;
  /* background-image: url('./images/codingBG.jpg');
  background-repeat: no-repeat;
  background-size: cover; */
  background-color: #000;
}

@media (max-width: 500px) {
  .nameAndSocial {
    width: 100%;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.nameAndJob h1 {
  font-size: 100px;
  margin-bottom: 0;
  font-family: "Darker Grotesque";
  font-weight: bold;
}

@media (max-width: 500px) {
  .nameAndJob h1 {
    font-size: 70px;
    width: 100%;
  }
}

.nameAndJob {
  color: white;
  display: flex;
  justify-content: center;
  text-align: left;
}

@media (max-width: 500px) {
  .nameAndJob {
    text-align: center;
    color: white;
    margin-top: 5rem;
  }
}

.nameAndJob h3 {
  font-size: 25px;
  font-family: "Darker Grotesque";
  font-style: italic;
  margin-top: 2%;
  color: #17bbeb;
}

.fa-level-down-alt {
  margin-top: 12%;
  color: white;
}

@media (max-width: 500px) {
  .fa-level-down-alt {
    margin: 12rem 0;
  }
}

.levelDownWhiteSpace {
  margin: 16rem 44rem;
}

@media (max-width: 500px) {
  .levelDownWhiteSpace {
    margin: 0rem 0rem;
  }
}

.fa-level-down-alt:hover {
  animation: move-up-down 1s infinite;
  color: #ef3e36;
  cursor: pointer;
}

@keyframes move-up-down {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
}

/* Social Media CSS and Animations */
.socialMedia {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 2%;
  top: 35%;
  color: white;
}

@media (max-width: 500px) {
  .socialMedia {
    flex-direction: row;
    justify-content: space-evenly;
    position: static;
    margin-top: 5rem;
  }
}

.socialMedia a,
.socialMedia:visited {
  color: white;
}

.fa-github-alt,
.fa-linkedin-in {
  margin-bottom: 2rem;
}

.fa-github-alt:hover,
.fa-linkedin-in:hover,
.fa-twitter:hover,
.fa-home:hover {
  animation: shake 1s infinite;
  color: #17bbeb;
  cursor: pointer;
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

/* Particle BG CSS */
.particles {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  z-index: -1;
}

/* About me CSS */
.about-me {
  background-color: white;
}

.about-me-content {
  height: 1000px;
  width: 800px;
  margin: 0 auto;
}

@media (max-width: 500px) {
  .about-me-content {
    width: 95%;
  }
}

.about-me-content h1 {
  padding: 10% 0;
  font-size: 65px;
  font-weight: bold;
  font-family: "Darker Grotesque";
}

.about-me-content p {
  font-size: 24px;
  font-family: "Darker Grotesque";
}

/* Arrows and Arrow Animations */
.left-right-arrow {
  margin-top: 15%;
  display: flex;
  justify-content: space-between;
}

.fa-chevron-down {
  animation: move-up-down 1s infinite;
}

@media (max-width: 500px) {
  .fa-chevron-down {
    animation: move-up-down 0;
  }
}

.fa-chevron-left:hover {
  animation: move-left-right 1s infinite;
  color: #ef3e36;
  cursor: pointer;
}

.fa-chevron-right:hover {
  animation: move-left-right 1s infinite;
  color: #ef3e36;
  cursor: pointer;
}

.allArrows {
  width: 800px;
  margin: 0 auto;
}

@media (max-width: 500px) {
  .allArrows {
    width: 95%;
  }
}

.arrowCategory {
  display: flex;
  align-items: center;
}

.projects-text {
  padding-right: 6rem;
  animation: color-loop 1s infinite;
  color: #333533;
}

.projects-text:hover {
  cursor: pointer;
}

.mobile-instruction {
  display: none;
}

@media (max-width: 500px) {
  .mobile-instruction {
    display: block;
    padding: 0 1rem;
  }
}

@media (max-width: 500px) {
  .projects-text {
    padding-right: 0rem;
    animation: 0;
    color: #ef3e36;
  }
}

.resume-text {
  padding-left: 6rem;
  animation: color-loop 1s infinite;
  color: #333533;
}

.resume-text:hover {
  cursor: pointer;
}

@media (max-width: 500px) {
  .resume-text {
    padding-left: 0rem;
    animation: 0;
    color: #ef3e36;
  }
}

@media (max-width: 500px) {
  .arrow-instructions {
    display: none;
  }
}

@keyframes color-loop {
  0% {
    color: #000000;
  }
  50% {
    color: #ef3e36;
  }
}

@keyframes move-left-right {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-10px);
  }
}

@keyframes move-right-left {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px);
  }
}

/* Projects and animation */
.projects {
  animation: slide-in-left 3s;
  animation-fill-mode: forwards;
}

@media (max-width: 500px) {
  .projects {
    width: 100%;
    z-index: 1;
  }
}

.project-links a {
  color: #000;
  text-decoration: none;
  font-size: 22px;
  font-family: "Darker Grotesque";
}

.project-links a:hover {
  color: #ef3e36;
  animation: hover-large 2s infinite;
}

@keyframes hover-large {
  0% {
    font-size: 22px;
  }
  50% {
    font-size: 26px;
  }
}

@keyframes slide-in-left {
  0% {
    transform: translateX(1100px);
  }
  50% {
    transform: translateX(0px);
  }
}

/* Resume and Animation */
.resume {
  animation: slide-in-right 3s;
  animation-fill-mode: forwards;
}
.resume img {
  width: 100%;
}

@media (max-width: 500px) {
  .resume {
    z-index: 1;
  }
  .resume img {
    width: 340px;
  }
}

@keyframes slide-in-right {
  0% {
    transform: translateX(-1100px);
  }
  50% {
    transform: translateX(0px);
  }
}

.project-card {
  display: flex;
  align-items: center;
  margin-bottom: 100px;
  justify-content: space-evenly;
}

@media (max-width: 500px) {
  .project-card {
    flex-direction: column-reverse;
    margin: 0 1.5rem;
  }
}

.project-card img {
  width: 500px;
}

@media (max-width: 500px) {
  .project-card img {
    margin-bottom: 1rem;
    width: 360px;
  }
}

.text-content h2 {
  font-size: 40px;
  font-weight: bold;
  font-family: "Darker Grotesque";
  margin-bottom: 30px;
  text-decoration: underline;
  color: #17bbeb;
}

.project-links {
  display: flex;
  justify-content: space-evenly;
  margin-top: 15px;
}

@media (max-width: 500px) {
  .project-links {
    margin-bottom: 1.5rem;
  }
}
